<template>
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headButton">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="setUp"></Button>

			</div>
		</div>

		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable spaceTable">
					<div class="tableDiv">
						<el-table ref="multipleTable" stripe :data="rows" tooltip-effect="dark" style="width: 100%;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column type="selection" width="55">
							</el-table-column>
							<el-table-column prop="name" label="分区">
							</el-table-column>
							<el-table-column label="省份" show-overflow-tooltip>
								<template slot-scope="scope">
									<span style="margin-left: 10px" v-for="o in scope.row.areaComRegionList">{{o.province}}&nbsp;&nbsp;</span><br />
								</template>
							</el-table-column>

						</el-table>
					</div>
				</div>
				<div class="footerDiv">

				</div>
			</div>
			<!--新增弹出框-->
			<el-dialog :title="title" :visible.sync="plus" width='40%' center>
				<el-form :model="form">
					<el-form-item label="路径" :label-width="formLabelWidth">
						<!--<el-input v-model="form.target" placeholder="请输入路劲" auto-complete="off"></el-input>-->
						<span v-text="name"></span>
					</el-form-item>
					<el-form-item label="省份" :label-width="formLabelWidth">
						<el-select v-model="form.dealerCodes" multiple filterable remote placeholder="请输入省份" :remote-method="remoteMethod" :loading="loading" clearable @change="choise">
							<el-option v-for="item in options4" :key="item.regionCode" :label="item.regionName" :value="item.regionCode">
							</el-option>
						</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<SolidButton :type='"hollow"' :text='"取消"' @click="plus = false"></SolidButton>
					<SolidButton :type='"solid"' :text='"确认"' @click="confirm"></SolidButton>
				</div>
			</el-dialog>
		</div>
	</div>

</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			SolidButton,
		},
		data() {
			return {
				title: '设置',
				id: '',
				name: '',
				rows: [],
				pageNum: '1',
				pageRows: '10',
				brandName: [],
				form: {
					target: '',
					dealerCodes: ''
				},
				list: '',
				plus: false,
				formLabelWidth: '10%',
				options4: [], //经销商模糊搜索数据数组
				loading: false,
				regionCodes: '',
				add: {
					text: '设置', //按钮名称
					type: 'icon-setting', //按钮颜色class,及图标
				},
			}
		},
		methods: {
			//表格Checkbox 选中
			handleSelectionChange(val) {
				this.list = val;
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//初始化数据
			loadData() {
				var self = this;
				self.$api.post('/crm/association/queryList', {}, r => {
					self.rows = r.repData
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			// 模糊搜索
			remoteMethod(query) {
				var self = this;
				if(query !== '') {
					this.loading = true;
					setTimeout(() => {
						self.$api.post('/crm/region/doProvinceBylike', {
							regionName: query,
							pageNum: 1,
							pageRows: 10
						}, res => {
							if(res.repData.length > 0) {
								self.options4 = res.repData
							} else {
								//								this.reqParams.missionName = "";
								this.options4 = [];
							}
							this.loading = false
						}, error => {
							this.$message({
								message: e.repData.repMsg,
								type: 'error'
							});
						})
					}, 200);
				} else {
					this.options4 = [];
				}
			},
			choise() {
				console.log(this.form.dealerCodes)
			},
			//编辑
			setUp() {
				if(this.list != '') {
					if(this.list.length > 1) {
						this.$message({
							message: '只能选择一条',
							type: 'warning'
						});
						return;
					} else {
						this.plus = true
						var arr = [];
						this.name = this.list[0].name
						this.id = this.list[0].id
						for(var i = 0; i < this.list[0].areaComRegionList.length; i++) {
							arr.push(this.list[0].areaComRegionList[i].regionCode)
							this.options4.push({
								regionName: this.list[0].areaComRegionList[i].province,
								regionCode: this.list[0].areaComRegionList[i].regionCode
							})
						}
						this.form.dealerCodes = arr;
						this.regionCodes = arr;
						console.log(this.regionCodes)
					}
				} else {
					this.$message({
						message: '请选择要编辑的数据',
						type: 'warning'
					});
					return;
				}

			},
			//确认
			confirm() {
				this.$api.post('/crm/association/insertOrUpdate', {
					id: this.id,
					regionCodes: this.form.dealerCodes
				}, r => {
					this.$message({
						message: '编辑成功',
						type: 'success'
					});
					this.plus = false;
					this.loadData();
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			}
		},
		created() {
			this.loadData();
		},
	}
</script>

<style scoped="scoped">
	/*0、整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
</style>


